<script>
import PrimaryButton from "@/components/PrimaryButton";
import PrimaryToggleButton from "@/components/PrimaryToggleButton";

export default {
  name: "AutoQuantumTab",
  components: {
    PrimaryButton,
    PrimaryToggleButton
  },
  data() {
    return {
      auto: false,
      mode: AUTO_QUANTUM_TYPE.AMOUNT,
      amount: new Decimal(-1),
      time: 0,
      amountText: "",
      init: false
    };
  },
  computed: {
    buttonText() {
      const name = this.isAmount ? "Amount" : "Time";
      return i18n.t("auto_quantum_mode_X", { value: name });
    },
    isAmount() {
      return this.mode === AUTO_QUANTUM_TYPE.AMOUNT;
    }
  },
  watch: {
    auto(value) {
      Autobuyer.quantum.isActive = value;
    },
    mode(value) {
      Autobuyer.quantum.mode = value;
    },
    amount(value) {
      Autobuyer.quantum.amount = value;
      this.amountText = format(value);
    },
    time(value) {
      Autobuyer.quantum.time = value;
    },
  },
  methods: {
    update() {
      this.auto = Autobuyer.quantum.isActive;
      this.mode = Autobuyer.quantum.mode;
      this.amount.copyFrom(Autobuyer.quantum.amount);
      this.time = Autobuyer.quantum.time;
      if (!this.init) {
        this.amountText = format(this.amount);
        this.init = true;
      }
    },
    toggleMode() {
      if (this.isAmount) {
        this.mode = AUTO_QUANTUM_TYPE.TIME;
      } else {
        this.mode = AUTO_QUANTUM_TYPE.AMOUNT;
      }
    },
    updateAmount() {
      const amount = Decimal.fromString(this.amountText);
      if (!amount.isFinite() || amount.lt(0)) return;
      this.amount = amount;
    }
  }
};
</script>

<template>
  <div>
    <div>
      <PrimaryToggleButton
        v-model="auto"
        i18n-key="auto_quantum_X"
      />
      <PrimaryButton @click="toggleMode">
        {{ buttonText }}
      </PrimaryButton>
    </div>
    <div>
      <input
        v-if="isAmount"
        v-model="amountText"
        class="o-weight-input"
        @blur="updateAmount"
      >
      <input
        v-else
        v-model="time"
        class="o-weight-input"
        type="number"
      >
      <span v-if="isAmount">={{ format(amount) }}</span>
      <span v-else>={{ format(time) }}s</span>
    </div>
  </div>
</template>

<style scoped>
.o-weight-input {
  display: flex;
  background: none;
  border: 1px solid var(--color-accent);
  margin: 0 3px;
  font-family: BlobFont;
  outline: none;
  color: var(--color-accent);
}
</style>